body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background {
    background: rgba(246,41,12,1);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(246,41,12,1)), color-stop(0%, rgba(240,47,23,1)), color-stop(0%, rgba(41,102,255,1)), color-stop(33%, rgba(41,102,255,1)), color-stop(100%, rgba(25,209,255,1)));
    background: linear-gradient(45deg, rgba(246,41,12,1) 0%, rgba(240,47,23,1) 0%, rgba(41,102,255,1) 0%, rgba(41,102,255,1) 33%, rgba(25,209,255,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f6290c', endColorstr='#19d1ff', GradientType=1 );
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

input[type="checkbox"] {
  display: none;
}
label {
  padding-left: 20px;
}
label::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin-left: -10px;
  margin-right: 10px;
  border-radius: 50%;
  border: 1px solid white;
}
input[type="checkbox"]:checked + label::before {
  content: '';
  background-size: 100%;
  background-image: url(data:image/svg+xml;utf8;base64,)
}

input[type="checkbox"]:checked + label {
  text-decoration: line-through;
}

.slideRight{
	animation-name: slideRight;
	-webkit-animation-name: slideRight;	

	animation-duration: 1s;	
	-webkit-animation-duration: 1s;

	animation-timing-function: ease-in-out;	
	-webkit-animation-timing-function: ease-in-out;		

	visibility: visible !important;	
}

@keyframes slideRight {
	0% {
		-webkit-transform: translateX(-150%);
		        transform: translateX(-150%);
	}
	50%{
		-webkit-transform: translateX(8%);
		        transform: translateX(8%);
	}
	65%{
		-webkit-transform: translateX(-4%);
		        transform: translateX(-4%);
	}
	80%{
		-webkit-transform: translateX(4%);
		        transform: translateX(4%);
	}
	95%{
		-webkit-transform: translateX(-2%);
		        transform: translateX(-2%);
	}			
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}	
}

@-webkit-keyframes slideRight {
	0% {
		-webkit-transform: translateX(-150%);
	}
	50%{
		-webkit-transform: translateX(8%);
	}
	65%{
		-webkit-transform: translateX(-4%);
	}
	80%{
		-webkit-transform: translateX(4%);
	}
	95%{
		-webkit-transform: translateX(-2%);
	}			
	100% {
		-webkit-transform: translateX(0%);
	}
}

.task {
	width: 100%;
	min-height: 30px;
	margin-bottom: 10px;
	overflow: visible;
	display: flex;
	justify-content: space-between;
	position: relative;
	align-items: center;
	background: #19CCFF;
	box-shadow: 2px 1px 8px 0px rgba(0,0,0,0.26);
}

.task label {
	max-width: 70%;
	margin-left: 50px;
	overflow: visible;
	min-height: 20px;
	max-height: 900px;
}

.task label:before {
	position: absolute;
	top: 25%;
	left: 25px;
}

p {
	word-wrap: break-word;
	color: white;
}

.tasks-container {
	width: 90%;
	height: 350px;
	overflow-y: scroll;
	overflow-x: hidden;
}

.task button {
	height: 60px;
	width: 100px;
	background: white;
	font-size: 20px;
	color: #19CCFF;
	border: none;
	cursor: pointer;
}
.form  {
    background: #fff;
    width: 600px;
    height: 600px;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
box-shadow: 0px 3px 33px -1px rgba(0,0,0,0.65);
}
.addInp {
    width: 300px;
    height: 35px;
    padding-left: 20px;
    padding-right: 20px;
    color: #000;
    border-radius: 25px;
    border: 1px solid #c8c8c8;
    outline: none;
    font-size: 20px;
    transition: 0.2s ease;
}

.addInp::-webkit-input-placeholder {
    opacity: 0.6;
    color: #2484FF;
    font-size: 20px;
}

.addInp::-ms-input-placeholder {
    opacity: 0.6;
    color: #2484FF;
    font-size: 20px;
}

.addInp::placeholder {
    opacity: 0.6;
    color: #2484FF;
    font-size: 20px;
}

* {
    box-sizing: border-box;
}
.add-tasks {
    display: flex;
    justify-content: space-between;
}

.addInp:focus {
    border: 1px solid #000;
}

.addButton {
    height: 35px;
    width: 70px;
    background: #2484FF;
    font-weight: bold;
    font-size: 17px;
    color: white;
    border: none;
    margin-left: 20px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    transition: 0.4s ease;
}

.addButton:hover {
    border: 1px solid #2484FF;
    background: #fff;
    color: #2484FF;
}

.addButton:hover span{
    color: #2484FF;
    font-size: 21px;
}

.addButton span {
    font-size: 20px;
    font-weight: bold;
    color: white;
    vertical-align : middle;
    transition: 0.4s ease;
}
